* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  position: relative;
  height: 100vh;
  width: 100vw;
  font-family: "Bungee", cursive;
  overflow-x: hidden;
}

a:-webkit-any-link {
  text-decoration: none;
  color: inherit;
}

/* -------------TEAM SECTION STYLING------------- */

.font-simple {
  font-family: "Space Grotesk", sans-serif;
}
.bg-robot-1 {
  position: relative;
  background: url(../images/1.png) center no-repeat;
  background-size: cover;
  height: 500px;
  border-radius: 8px;
  cursor: pointer;
}
.bg-robot-2 {
  position: relative;
  background: url(../images/2.png) center no-repeat;
  background-size: cover;
  height: 500px;
  border-radius: 8px;
  cursor: pointer;
}
.bg-robot-3 {
  position: relative;
  background: url(../images/3.png) center no-repeat;
  background-size: cover;
  height: 500px;
  border-radius: 8px;
  cursor: pointer;
}
.bg-robot-4 {
  position: relative;
  background: url(../images/4.png) center no-repeat;
  background-size: cover;
  height: 500px;
  border-radius: 8px;
  cursor: pointer;
}
.black-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
}
.hover-overlay:hover {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    rgba(189, 165, 68, 0.7) 10%,
    rgba(0, 0, 0, 0.7) 100%
  );
}
.community-bg {
  background: url(../images/cover.jpg) no-repeat;
  background-position: top 30% center;
  background-size: cover;
}

@media (max-width: 1024px) {
  .community-bg {
    background: url(../images/cover.jpg) center no-repeat;
    background-size: auto;
  }
}

